.macbook-pro {
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.macbook-pro .SPLASH-wrapper {
  background-color: #ffffff;
  border: 1px none;
  height: 100vh;
  position: relative;
  width: 100%;
}

.macbook-pro .SPLASH {
  height: 100vh;
  left: 0;
  background-size: cover;
  position: absolute;
  top: 0;
  width: 100%;
}
